import React from 'react';
import {
  Input,
  Button
} from '@ya.praktikum/react-developer-burger-ui-components'
import { useSelector, useDispatch} from '../../types'
import {patchUser} from '../../services/actions/registration'
import {Redirect} from 'react-router-dom'



export const ProdileEdit = () => {
  const dispatch = useDispatch()

  const user = useSelector(state => state.registration.user)
  let data 
  if (user) {
    data = {...user, password: ''}
  } else {
    data = {name:'',email:'',password:'',}
  }
  const [value, setValue] = React.useState(data)
  const onChange = (e:React.ChangeEvent<HTMLInputElement>) => {
    setValue({...value, [e.target.name]: e.target.value})
  }
  const cancel = () => {
    console.log('cancel - tbc')
    
  }
  const save = (e:any) => {
    e.preventDefault()
    dispatch(patchUser(value))
  }
  
  // useEffect(() => {
  //   dispatch(getItems())
  // }, [dispatch])
  if (!data) {
    return <Redirect to='/login' />
  }
  return (
    <form onSubmit={(e) => save(e)}>
    <Input
      type={'text'}
      placeholder={''}
      onChange={e => onChange(e)}
      icon={'EditIcon'}
      value={value?.name}
      name={'name'}
      error={false}
      errorText={''}
      size={'default'} />

    <Input
      type={'text'}
      placeholder={'E-mail'}
      onChange={e => onChange(e)}
      icon={'EditIcon'}
      value={value?.email}
      name={'email'}
      error={false}
      errorText={''}
      size={'default'} />

    <Input
      type={'text'}
      placeholder={''}
      onChange={e => onChange(e)}
      icon={'EditIcon'}
      value={value.password}
      name={'password'}
      error={false}
      errorText={''}
      size={'default'} />

    <Button type="secondary" onClick={() => cancel()}></Button>
    <Button></Button>
    </form>
  );
}

