import orderDoneImg from '../../images/order-done.svg';
import { useSelector } from '../../types'

import styles from './order-details.module.css'


const OrderDetails = () => {
  const order = useSelector(state => state.burgerConstructor.order)
  return (
    <>
    {
      order
      ?
      <div className={styles.main}>
        <p className={`${styles.wrapped} text text_type_digits-large`}>
          {order.order.number}
        </p>
        <p className="text text_type_main-medium"> </p>
        <img className={styles.image} src={orderDoneImg} alt="Done!"/>
        <p className="text text_type_main-default">   </p>
        <p className="text text_type_main-default">    </p>
      </div>
      :
      <div>,   ...</div>
    }

      
    </>
  );
}



export default OrderDetails;

